import React from 'react';
import { CloseOutlined } from '@ant-design/icons';
import { narrativespec2Vis } from '@/tool/narrative2cards';
import { fact2unitVis } from '@/tool/fact2unitVis';

import uuidv4 from 'uuid/v4';
import ActionType from '@/action/type';
import ChartType from '../../../../constant/ChartType';
import * as api from '@/axios/api';
import OperationType from '@/constant/OperationType';
import config from '@/axios/config';
import Fact from '@/model/fact';
import { genFactSentence } from '@/sentencer/index';
import { isValid, customizeFact } from '@/view/FactEditor/helper';
import getSupportedChartTypes from '@/tool/getSupportedChartTypes';
import _, { values } from 'lodash';
import { Divider, Button, Tooltip, Tag, Slider ,Spin, Menu, Dropdown,Input} from "antd";
import { DownOutlined } from '@ant-design/icons';

const { SubMenu } = Menu;

export default class SingleOperator extends React.Component {

    searchIndex = (array,target,rank)  => {
        var ranks = []
        for (var i = 0; i < array.actions.length; i++) {
          if (array.actions[i].add == target) {
            ranks.push(i);
          }
        }
        return ranks[rank]
      }
    
    /* ******************************** click ********************************  */
    // ReferenceStep
    referenceStepOnClick = (e) => {
        var temp = _.cloneDeep(this.props.temp_spec)
        var rank = _.cloneDeep(this.props.index)
        var newValue = '#'+e.key
        var reR = /\#[0-9]*/g
        var preRef = temp.operators[rank].args
        var nexRef = preRef.replace(reR,newValue)
        temp.operators[rank].args = nexRef
        this.props.changeTempSpec(temp)
        this.props.operatorChange(true)
      };

    // Title
    changeTitle = (e) => {
        var temp = _.cloneDeep(this.props.temp_spec)
        var rank = _.cloneDeep(this.props.index)
        var newValue = e.target.value
        var tempIndex = this.searchIndex(this.props.temp_spec,'title',rank)

        temp.actions[tempIndex].style.text = newValue
        this.props.changeTempSpec(temp)
        this.props.animationChange(true)
    }

    // Duration
    changeDuration = (e) => {
        var temp = _.cloneDeep(this.props.temp_spec)
        var rank = _.cloneDeep(this.props.index)
        var newValue = e.target.value
        var tempIndex = this.searchIndex(this.props.temp_spec,'title',rank)

        temp.actions[tempIndex].style.duration = newValue
        this.props.changeTempSpec(temp)
        this.props.animationChange(true)
    }
    
    // Caption
    changeCaption = (e,index) => {
        /* console.log('change title :' + e.target.value)
        console.log('this.props.temp_spec:',this.props.temp_spec)

        var temp = _.cloneDeep(this.props.temp_spec)
        var rank = _.cloneDeep(this.props.index)
        var newValue = e.target.value
        var tempIndex = this.searchIndex(this.props.temp_spec,'caption',rank)

        temp.actions[tempIndex].style.text = newValue
        this.props.changeTempSpec(temp)
        console.log('this.props.temp_spec',temp) */
    }

    // Compare
    CompareStatusOnClick = (e) => {
        var temp = _.cloneDeep(this.props.temp_spec)
        var rank = _.cloneDeep(this.props.index)
        switch(e.key){
            case('0'):
                var newValue = '>';
                break;
            case('1'):
                var newValue = '>=';
                break;
            case('2'):
                var newValue = '<';
                break;
            case('3'):
                var newValue = '<=';
                break;
            case('4'):
                var newValue = '=';
                break;
            case('5'):
                var newValue = '!=';
                break;
        }
        var reR = /<=|>=|!=|=|>|</g
        var preRef = temp.operators[rank].args
        var nexRef = preRef.replace(reR,newValue)
        temp.operators[rank].args = nexRef
        this.props.changeTempSpec(temp)
        this.props.operatorChange(true)
      };

    
    render() {
        const { index,spec,fact,captionDistribution } = this.props
        const { TextArea } = Input;
        const myAttributeOptions = spec.data.schema

        /* ******************************** variable ********************************  */
        // ReferenceStep
        var reR = /\#[0-9]*/g
        var referencestep = spec.operators.args
        var myReference = referencestep.match(reR)
        if(myReference)
            var referenceNum = myReference[myReference.length-1]
        else
            var referenceNum = null

        // Attribute
        var reA = /(?<=\|)\w*?(?=\,|\])/g
        var myAttribute = spec.operators.args.match(reA)
        if(myAttribute)
            var myAttribute = myAttribute[myAttribute.length-1]
        else
            var myAttribute = null

        // Title

        // Chanel

        // Duration

        // Caption
        var captions = []
        spec.actions.map((item, index) => {
            if(item.add == 'caption'){
                if(!captions.includes(item))
                    {captions.push(item)}
            }
        })

        // Annotation
        var annotations = []
        spec.actions.map((item, index) => {
            if(item.add == 'annotation'){
                if(!annotations.includes(item))
                    {annotations.push(item)}
            }
        })

        // Compare
        var comparativeStatus = null
        var operatorArg = spec.operators.args
        var comparativeStatusTemp = operatorArg.match(/<=|>=|!=|=|>|</g)
        if(comparativeStatusTemp){
            var comparativeStatus = comparativeStatusTemp[0]
            if(operatorArg.match(/\|[0-9]*\|/g))
                {var comparativeNumberString = operatorArg.match(/\|[0-9]*\|/g)[0]
                var comparativeNumber = comparativeNumberString.substring(1,comparativeNumberString.length-1)}
            else
                {var comparativeNumberString = operatorArg.match(/=\|\w*\|/g)[0]
                var comparativeNumber = comparativeNumberString.substring(2,comparativeNumberString.length-1)}
            switch(comparativeStatus){
                case('<'):
                    comparativeStatus='less';
                    break;
                case('>'):
                    comparativeStatus='great';
                    break;
                case('<='):
                    comparativeStatus='lessequal';
                    break;
                case('>='):
                    comparativeStatus='greatequal';
                    break;
                case('='):
                    comparativeStatus='equal';
                    break;
                case('!='):
                    comparativeStatus='notequal';
                    break;
            }
        }
        

        /* ******************************** options ********************************  */
        // options of ReferenceStep
        var referenceArray = [1]
        if(referenceNum){
            var referenceLine = new String(referenceNum)
            var referenceMax = parseInt(referenceLine[1])
            var referenceMaxT = referenceMax+1
            var referenceArray = Object.keys(Array.apply(null, {length:referenceMaxT}));
            referenceArray.splice(0,1)
        }
        const menuReferenceStep = (
            <Menu onClick={e => this.referenceStepOnClick(e)}>
                {referenceArray.map((item,index)=>{
                    return(
                    <Menu.Item key={item}>
                        #{item}
                    </Menu.Item>
                )})
                }
            </Menu>
        );

        // options of Attribute
        const menuAttribute = (
            <Menu>
                {myAttributeOptions.map((item,index)=>{
                    return(
                    <Menu.Item key={index}>
                        {item.field}
                    </Menu.Item>
                )})
                }
            </Menu>
        );
        
        // options of Chanel
        var myAttributeType = 'categorical'
        myAttributeOptions.map((item,index)=>{
            if(item.field==myAttribute)
                {myAttributeType = item.type}
        })
        var chanels = []
        if(myAttributeType == 'categorical')
            {chanels.push('x')
            chanels.push('y')}
        else
            {chanels.push('size')}

        const menuChanel = (
            <Menu>
                {chanels.map((item,index)=>{
                    return(
                        <Menu.Item key={index}>
                            {item}
                        </Menu.Item>
                    )
                })}
            </Menu>
        );

        // options of Compare
        const menuCompare = (
            <Menu onClick={e => this.CompareStatusOnClick(e)}>
                <Menu.Item key={0}>
                    great
                </Menu.Item>
                <Menu.Item key={1}>
                    greatequal
                </Menu.Item>
                <Menu.Item key={2}>
                    less
                </Menu.Item>
                <Menu.Item key={3}>
                    lessequal
                </Menu.Item>
                <Menu.Item key={4}>
                    equal
                </Menu.Item>
                <Menu.Item key={5}>
                    notequal
                </Menu.Item>
            </Menu>
        );

        // options of attribute in chanel
        const menuAttributeInEncoding = (
            <Menu>
                {myAttributeOptions.map((item,index)=>{
                    return(
                    <Menu.Item key={index}>
                        {item.field}
                    </Menu.Item>
                )})
                }
            </Menu>
        )
        

        return (
            <div className='SingleOperator'>
                <div className='LineOne'>
                    {referenceNum?
                    <div className='ReferenceStep'>
                        <div>
                            <p>Reference Step</p>
                        </div>
                        <Dropdown overlay={menuReferenceStep}>
                            <Button style={{ width: "100%"}}>
                            {referenceNum} <DownOutlined />
                            </Button>
                        </Dropdown>
                    </div>
                    :
                    <div className='ReferenceStep' style={{width : 0}}>
                        
                    </div>
                    }
                    {myAttribute?
                        referenceNum?
                        <div className='Attribute'>
                            <div>
                                <p>Attribute</p>
                            </div>
                            <Dropdown overlay={menuAttribute}>
                                <Button style={{ width: "100%"}}>
                                {myAttribute}<DownOutlined />
                                </Button>
                            </Dropdown>
                        </div>
                        :
                        <div className='Attribute' style={{paddingLeft : 0}}>
                            <div>
                                <p>Attribute</p>
                            </div>
                            <Dropdown overlay={menuAttribute}>
                                <Button style={{ width: "100%"}}>
                                {myAttribute}<DownOutlined />
                                </Button>
                            </Dropdown>
                        </div>
                    :
                    <div className='Attribute'>
                        
                    </div>
                    }
                </div>
                {comparativeStatus?
                <div className='LineTwo'>
                    <div className='Comparative'>
                        <div className='Compare'>
                            <div>
                                <p>Compare</p>
                            </div>
                            <Dropdown overlay={menuCompare}>
                                <Button style={{ width: "100%"}}>
                                {comparativeStatus} <DownOutlined />
                                </Button>
                            </Dropdown>
                        </div>
                        <div className='CompareNum'>
                            <div>
                                <p>Number</p>
                            </div>
                            <TextArea defaultValue={comparativeNumber} autoSize/>
                        </div>
                    </div>
                </div>
                    :
                <div>
                    
                </div>
                }
                <div className='LineThree'>
                    <div className='Actions'>
                        <div>
                            <p>Actions</p>
                        </div>
                        <Menu
                            style={{ width: "100%" ,backgroundColor:"white"}}
                            mode="inline">
                            <SubMenu title="Add Title" style={{marginLeft:-45}}>
                                <div className='Title'>
                                    <div>
                                        <p>Title</p>
                                    </div>
                                    <TextArea defaultValue={spec.actions[spec.actions.length-1].style.text} autoSize onPressEnter={e => this.changeTitle(e)} />
                                </div>
                                {/* <div className='myDuration'>
                                    <div>
                                        <p>Duration</p>
                                    </div>
                                    <TextArea defaultValue={spec.actions[spec.actions.length-1].style.duration} autoSize onPressEnter={e => this.changeDuration(e)} />
                                </div> */}
                            </SubMenu>
                            {spec.actions[0].channel?
                            <SubMenu title="Add Encoding" style={{marginLeft:-45}}>
                                <div className='myChanel'>
                                    <div>
                                        <p>Chanel</p>
                                    </div>
                                    <Dropdown overlay={menuChanel}>
                                        <Button style={{ width: "100%"}}>
                                        {spec.actions[0].channel} <DownOutlined />
                                        </Button>
                                    </Dropdown>
                                </div>  
                                <div className='myAttribute'>
                                    <div>
                                        <p>Attribute</p>
                                    </div>
                                    <Dropdown overlay={menuAttributeInEncoding}>
                                        <Button style={{ width: "100%"}}>
                                        {spec.actions[0].field.field} <DownOutlined />
                                        </Button>
                                    </Dropdown>
                                </div>
                                {/* <div className='myDuration'>
                                    <div>
                                        <p>Duration</p>
                                    </div>
                                    <TextArea defaultValue={spec.actions[spec.actions.length-1].style.duration} autoSize onPressEnter={e => this.changeDuration(e)} />
                                </div> */}
                                {/* {captions.map((item, index) => {
                                return(
                                    <div className='myCaption' key={index}>
                                        <div>
                                            <p>Caption</p>
                                        </div>
                                        <TextArea defaultValue={item.style.text} autoSize
                                        onPressEnter={e => this.changeCaption(e,index)}/>
                                    </div>
                                )})} */}
                            </SubMenu>
                            :
                            <div></div>
                            }
                            {annotations.length!=0 ?
                                annotations.map((item, index) => {
                                    return(
                                    <SubMenu title="Add Annotation" style={{marginLeft:-45}}>
                                        <div className='annotationLineOne'>
                                            <div>
                                                <p>Method</p>
                                            </div>
                                            <TextArea defaultValue={item.method} autoSize/>
                                        </div>
                                        {/* <div className='annotationLineTwo'>
                                            <div>
                                                <p>duration</p>
                                            </div>
                                            <TextArea defaultValue={item.style.duration} autoSize/>
                                        </div> */}
                                        {item.style.text?
                                        <div className='annotationLineThree'>
                                            <div>
                                                <p>text</p>
                                            </div>
                                            <TextArea defaultValue={item.style.text} autoSize/>
                                        </div>
                                        :
                                        <div></div>
                                        }
                                    </SubMenu>
                                )})
                            :
                            <div></div>
                            }
                        </Menu>
                    </div>
                </div>
            </div>
        )
    }
}