import Chart from '@/Chart';
import React from 'react';



export const narrative_spec2operators = function(narrative_spec){
    let spec = {
        "chart" : narrative_spec.chart,
        "data": narrative_spec.data,
        "actions": [],
        "operators":[]
    }
    console.log(narrative_spec)
    let actions = []
    let idx_titleaction = []
    let step_actions = []
    let captions = []
    let operator = 0

    if(narrative_spec.actions){
        actions = _.cloneDeep(narrative_spec.actions)
        actions.map((item, index) => {
            if(item.add === "title" && item.style.duration !== -1)
            idx_titleaction.push(index)
            if(item.add === "caption")
            captions.push(index)
        })
        // duration delay = 0
        for(let q = 0;q<idx_titleaction.length;q++){
            if(q == 0)
                {var eachaction = actions.slice(0,idx_titleaction[q]+1)}
            else
                {var eachaction = actions.slice(idx_titleaction[q-1],idx_titleaction[q]+1)}
            eachaction.map((item) => {
                if(item.style){
                    // item.style.delay = 0
                    // item.style.duration = 0
                }

            })
            // remove extra title
            let deleteNum = 0 
            let deleteTitle = []
            eachaction.map((item, index) => {
                // add caption to eachaction
                captions.map((item1, num) => {
                    let markSpecial = actions[item1].style.mark
                    let markSpecialTemp = markSpecial.split('_')[0]
                    console.log('markSpecial'+markSpecial)
                    let mark = actions[item1].style.mark.split('_')[1];
                    if(markSpecialTemp === 'operator' && item.mark === 'unit')
                        {
                            eachaction.splice(-1,0,actions[item1])
                            deleteNum = deleteNum+1
                        }
                    if(mark === item.method || mark === item.channel)
                        {
                            eachaction.splice(-1,0,actions[item1])
                            deleteNum = deleteNum+1
                        }
                })
                if (item.add === "title" && index !== eachaction.length-1){
                    deleteTitle.push(index)
                }
            })
            let p = 0
            for(let i =0;i<deleteTitle.length;i++){
                let deletePosition = deleteTitle[i]+p
                eachaction.splice(deletePosition,1)
                p = p-1
            }
            captions.splice(0,deleteNum)
            let step_spec = _.cloneDeep(spec)

            step_spec.actions = eachaction
            // operator
            step_spec.operators = narrative_spec.operators[operator]
            operator = operator+1

            step_actions.push(step_spec)
        }


    // caption
    /* step_actions.map((item,index)=>{
        let tempLength = item.actions.length
        for(let i =0;i<tempLength;i++){
            if(item.actions[i].add == 'caption')
            {
                let j = i+1
                for(j;j<tempLength;j++){
                    if(item.actions[i].style.text == item.actions[j].style.text)
                        {
                            step_actions[index].actions.slice(j,1)
                            tempLength = tempLength-1
                            j = j-1
                        }
                }
            }
        }
    }) */
    }
    if(narrative_spec!='')
        {
            let lastOperator = narrative_spec.operators[narrative_spec.operators.length-2]
            if(lastOperator.type==null)
                step_actions.push(lastOperator)
        }

    return step_actions
}